
'use client';

import { useState, useTransition } from 'react';
import type { ExcelErrorDetectionOutput } from '@/ai/schemas';
import { analyzeExcelFile } from '@/lib/actions';
import { useToast } from '@/hooks/use-toast';
import { ConvertContent } from '@/components/convert-content';

export default function ConvertPage() {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [analysisResult, setAnalysisResult] = useState<ExcelErrorDetectionOutput | null>(null);
  const [isAnalyzing, startTransition] = useTransition();
  const { toast } = useToast();

  const handleFileSelect = (file: File) => {
    setSelectedFile(file);
    setAnalysisResult(null); 
  };

  const handleClearFile = () => {
    setSelectedFile(null);
    setAnalysisResult(null);
  };

  const handleAnalyze = () => {
    if (!selectedFile) return;

    startTransition(async () => {
      const formData = new FormData();
      formData.append('file', selectedFile);

      const result = await analyzeExcelFile(formData);
      
      if (result.success && result.data) {
        setAnalysisResult(result.data);
      } else {
        toast({
          title: 'Analysis Failed',
          description: result.error || 'An unknown error occurred.',
          variant: 'destructive',
        });
        // Clear the file so the user can try again
        setSelectedFile(null);
      }
    });
  };

  const handleReset = () => {
    setSelectedFile(null);
    setAnalysisResult(null);
  };

  return (
    <ConvertContent
      selectedFile={selectedFile}
      analysisResult={analysisResult}
      isAnalyzing={isAnalyzing}
      onFileSelect={handleFileSelect}
      onClearFile={handleClearFile}
      onAnalyze={handleAnalyze}
      onReset={handleReset}
    />
  );
}
