
'use client';

import type { ExcelErrorDetectionOutput } from '@/ai/schemas';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { FileUploader } from '@/components/file-uploader';
import { AnalysisResults } from '@/components/analysis-results';
import { WandSparkles, Loader2 } from 'lucide-react';
import { Progress } from '@/components/ui/progress';
import { useState, useEffect } from 'react';

interface ConvertContentProps {
  selectedFile: File | null;
  analysisResult: ExcelErrorDetectionOutput | null;
  isAnalyzing: boolean;
  onFileSelect: (file: File) => void;
  onClearFile: () => void;
  onAnalyze: () => void;
  onReset: () => void;
}

export function ConvertContent({
  selectedFile,
  analysisResult,
  isAnalyzing,
  onFileSelect,
  onClearFile,
  onAnalyze,
  onReset,
}: ConvertContentProps) {
    const [progress, setProgress] = useState(0);

    useEffect(() => {
        let timer: NodeJS.Timeout;
        if (isAnalyzing) {
            setProgress(0);
            const duration = 2000; // 2 seconds
            const interval = 20; // update every 20ms
            const increment = (interval / duration) * 100;
            
            timer = setInterval(() => {
                setProgress(prev => {
                    if (prev >= 95) { // Stop just before 100 to show completion
                        clearInterval(timer);
                        return 95;
                    }
                    return prev + increment;
                });
            }, interval);
        } else {
            setProgress(0);
        }

        return () => {
            clearInterval(timer);
        };
    }, [isAnalyzing]);


  return (
    <div className="flex flex-col items-center justify-center min-h-full p-4 md:p-6">
      {!analysisResult ? (
        <Card className="w-full max-w-4xl animate-in fade-in-50 duration-500">
          <CardHeader>
            <CardTitle className="text-3xl font-bold font-headline">Convert Your Excel File</CardTitle>
            <CardDescription className="font-body">
              Upload your file, let our AI check it for issues, and then convert it to DAT or PDF format.
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="p-8 text-center border-2 border-dashed rounded-lg">
                <FileUploader 
                    onFileSelect={onFileSelect}
                    clearFile={onClearFile}
                    selectedFile={selectedFile}
                    isAnalyzing={isAnalyzing}
                />
            </div>
            
            {isAnalyzing && (
                <div className="space-y-2 text-center">
                    <Progress value={progress} className="w-full" />
                    <p className="text-sm text-muted-foreground animate-pulse">Our AI genie is analyzing your file...</p>
                </div>
            )}

            {selectedFile && !isAnalyzing && (
              <div className="flex justify-center">
                <Button onClick={onAnalyze} disabled={isAnalyzing} size="lg" className="font-bold">
                  {isAnalyzing ? (
                    <>
                      <Loader2 className="w-5 h-5 mr-2 animate-spin" />
                      Analyzing...
                    </>
                  ) : (
                    <>
                      <WandSparkles className="w-5 h-5 mr-2" />
                      Analyze with AI
                    </>
                  )}
                </Button>
              </div>
            )}
          </CardContent>
        </Card>
      ) : (
        <AnalysisResults analysis={analysisResult} onReset={onReset} />
      )}
    </div>
  );
}
